// src/features/line-dashboard/settings/DroneEarlyInformSettingsPage.jsx
"use client"

import * as React from "react"
import {
  IconAlertCircle,
  IconDeviceFloppy,
  IconPlus,
  IconRefresh,
  IconTrash,
  IconX,
} from "@tabler/icons-react"

import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { cn } from "@/lib/utils"

const TABLE_NAME = "drone_early_inform_v3"

function normalizeRow(row, fallbackLineId = "") {
  if (!row || typeof row !== "object") {
    return null
  }

  const id = row?.id ?? row?.ID ?? null
  if (id === null || id === undefined) {
    return null
  }

  const lineIdValue = row?.line_id ?? row?.lineId ?? fallbackLineId ?? ""
  const mainStepValue = row?.main_step ?? row?.mainStep ?? ""
  const customEndStepValue = row?.custom_end_step ?? row?.customEndStep ?? ""

  return {
    id: String(id),
    lineId: typeof lineIdValue === "string" ? lineIdValue : String(lineIdValue ?? ""),
    mainStep: typeof mainStepValue === "string" ? mainStepValue : String(mainStepValue ?? ""),
    customEndStep:
      customEndStepValue === null || customEndStepValue === undefined
        ? ""
        : typeof customEndStepValue === "string"
          ? customEndStepValue
          : String(customEndStepValue ?? ""),
  }
}

function sortRows(rows) {
  return [...rows].sort((a, b) => {
    const mainA = a?.mainStep?.toLowerCase?.() ?? ""
    const mainB = b?.mainStep?.toLowerCase?.() ?? ""
    if (mainA < mainB) return -1
    if (mainA > mainB) return 1
    return Number.parseInt(a?.id ?? "", 10) - Number.parseInt(b?.id ?? "", 10)
  })
}

function removeKey(object, key) {
  if (!object || !(key in object)) return object
  const next = { ...object }
  delete next[key]
  return next
}

function sanitizeDraftValue(value) {
  if (value === null || value === undefined) return ""
  return String(value)
}

function sanitizeOptionalString(value) {
  if (value === null || value === undefined) return null
  const trimmed = String(value).trim()
  return trimmed.length === 0 ? null : trimmed
}

function sanitizeRequiredString(value) {
  const trimmed = sanitizeOptionalString(value)
  return trimmed ?? ""
}

export function DroneEarlyInformSettingsPage({ lineId }) {
  const [rows, setRows] = React.useState([])
  const [drafts, setDrafts] = React.useState({})
  const [isLoading, setIsLoading] = React.useState(false)
  const [error, setError] = React.useState(null)
  const [savingIds, setSavingIds] = React.useState({})
  const [deletingIds, setDeletingIds] = React.useState({})
  const [newRow, setNewRow] = React.useState({ mainStep: "", customEndStep: "" })
  const [createError, setCreateError] = React.useState(null)
  const [isCreating, setIsCreating] = React.useState(false)
  const [refreshToken, setRefreshToken] = React.useState(0)

  const hasLineId = typeof lineId === "string" && lineId.length > 0

  const fetchRows = React.useCallback(async () => {
    if (!hasLineId) {
      setRows([])
      setError(null)
      return
    }

    setIsLoading(true)
    setError(null)

    try {
      const params = new URLSearchParams({ lineId })
      const response = await fetch(`/api/drone-early-inform?${params.toString()}`, {
        cache: "no-store",
      })

      const payload = await response.json().catch(() => ({}))

      if (!response.ok) {
        const message = typeof payload?.error === "string" ? payload.error : "Failed to load settings"
        throw new Error(message)
      }

      const normalizedRows = Array.isArray(payload?.rows)
        ? payload.rows
            .map((row) => normalizeRow(row, lineId))
            .filter(Boolean)
        : []

      setRows(sortRows(normalizedRows))
      setDrafts({})
    } catch (fetchError) {
      const message = fetchError instanceof Error ? fetchError.message : "Failed to load settings"
      setError(message)
      setRows([])
      setDrafts({})
    } finally {
      setIsLoading(false)
    }
  }, [hasLineId, lineId])

  React.useEffect(() => {
    setRows([])
    setDrafts({})
    setError(null)
    setRefreshToken((token) => token + 1)
  }, [lineId])

  React.useEffect(() => {
    fetchRows()
  }, [fetchRows, refreshToken])

  const handleRefresh = React.useCallback(() => {
    fetchRows()
  }, [fetchRows])

  const handleDraftChange = React.useCallback((rowId, field, value) => {
    setDrafts((previous) => ({
      ...previous,
      [rowId]: {
        ...(previous[rowId] ?? {}),
        [field]: sanitizeDraftValue(value),
      },
    }))
  }, [])

  const handleCancelDraft = React.useCallback((rowId) => {
    setDrafts((previous) => removeKey(previous, rowId))
  }, [])

  const handleSaveRow = React.useCallback(
    async (rowId) => {
      const draft = drafts[rowId]
      const targetRow = rows.find((row) => row.id === rowId)
      if (!targetRow) return

      const nextMainStep = sanitizeRequiredString(draft?.mainStep ?? targetRow.mainStep)
      const nextCustomEndStep = sanitizeOptionalString(draft?.customEndStep ?? targetRow.customEndStep)

      const updates = {}
      if (nextMainStep !== targetRow.mainStep) {
        updates.mainStep = nextMainStep
      }
      if ((nextCustomEndStep ?? "") !== (sanitizeOptionalString(targetRow.customEndStep) ?? "")) {
        updates.customEndStep = nextCustomEndStep
      }

      if (Object.keys(updates).length === 0) {
        setDrafts((previous) => removeKey(previous, rowId))
        return
      }

      setSavingIds((previous) => ({ ...previous, [rowId]: true }))

      try {
        const response = await fetch("/api/drone-early-inform", {
          method: "PATCH",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ id: Number.parseInt(rowId, 10), updates }),
        })

        const payload = await response.json().catch(() => ({}))

        if (!response.ok) {
          const message = typeof payload?.error === "string" ? payload.error : "Failed to update row"
          throw new Error(message)
        }

        const normalized = normalizeRow(payload?.row, lineId)
        if (!normalized) {
          await fetchRows()
          return
        }

        setRows((previous) => sortRows(previous.map((row) => (row.id === rowId ? normalized : row))))
        setDrafts((previous) => removeKey(previous, rowId))
      } catch (saveError) {
        const message = saveError instanceof Error ? saveError.message : "Failed to update row"
        setError(message)
      } finally {
        setSavingIds((previous) => removeKey(previous, rowId))
      }
    },
    [drafts, fetchRows, lineId, rows]
  )

  const handleDeleteRow = React.useCallback(
    async (rowId) => {
      setDeletingIds((previous) => ({ ...previous, [rowId]: true }))

      try {
        const response = await fetch("/api/drone-early-inform", {
          method: "DELETE",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ id: Number.parseInt(rowId, 10) }),
        })

        const payload = await response.json().catch(() => ({}))

        if (!response.ok) {
          const message = typeof payload?.error === "string" ? payload.error : "Failed to delete row"
          throw new Error(message)
        }

        setRows((previous) => previous.filter((row) => row.id !== rowId))
        setDrafts((previous) => removeKey(previous, rowId))
      } catch (deleteError) {
        const message = deleteError instanceof Error ? deleteError.message : "Failed to delete row"
        setError(message)
      } finally {
        setDeletingIds((previous) => removeKey(previous, rowId))
      }
    },
    []
  )

  const handleNewRowChange = React.useCallback((field, value) => {
    setNewRow((previous) => ({ ...previous, [field]: value }))
  }, [])

  const handleCreateRow = React.useCallback(async () => {
    if (!hasLineId) return

    const mainStep = sanitizeRequiredString(newRow.mainStep)
    if (!mainStep) {
      setCreateError("Main Step은 필수입니다.")
      return
    }

    setIsCreating(true)
    setCreateError(null)

    const payloadBody = {
      lineId,
      mainStep,
      customEndStep: sanitizeOptionalString(newRow.customEndStep),
    }

    try {
      const response = await fetch("/api/drone-early-inform", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payloadBody),
      })

      const payload = await response.json().catch(() => ({}))

      if (!response.ok) {
        const message = typeof payload?.error === "string" ? payload.error : "Failed to create row"
        throw new Error(message)
      }

      const normalized = normalizeRow(payload?.row, lineId)
      if (normalized) {
        setRows((previous) => sortRows([...previous, normalized]))
      } else {
        await fetchRows()
      }

      setNewRow({ mainStep: "", customEndStep: "" })
    } catch (createErrorValue) {
      const message = createErrorValue instanceof Error ? createErrorValue.message : "Failed to create row"
      setCreateError(message)
    } finally {
      setIsCreating(false)
    }
  }, [fetchRows, hasLineId, lineId, newRow])

  const hasDraftChanges = React.useCallback(
    (rowId) => {
      const draft = drafts[rowId]
      if (!draft) return false
      const original = rows.find((row) => row.id === rowId)
      if (!original) return false

      const nextMain = sanitizeRequiredString(draft?.mainStep ?? original.mainStep)
      const nextEnd = sanitizeOptionalString(draft?.customEndStep ?? original.customEndStep)

      return (
        nextMain !== original.mainStep ||
        (nextEnd ?? "") !== (sanitizeOptionalString(original.customEndStep) ?? "")
      )
    },
    [drafts, rows]
  )

  const isBusy = isLoading || Object.keys(savingIds).length > 0 || Object.keys(deletingIds).length > 0

  return (
    <div className="flex h-full flex-col gap-4">
      <header className="flex flex-col gap-2 border-b border-border pb-4 sm:flex-row sm:items-center sm:justify-between">
        <div>
          <h1 className="text-2xl font-semibold tracking-tight">Line E-SOP Settings</h1>
          <p className="text-sm text-muted-foreground">
            {hasLineId ? `드론 얼리 인폼 테이블 (${TABLE_NAME}) · ${lineId}` : "라인 ID를 선택해주세요."}
          </p>
        </div>
        <div className="flex items-center gap-2">
          <Button variant="outline" onClick={handleRefresh} disabled={!hasLineId || isLoading}>
            <IconRefresh className="size-4" /> 새로고침
          </Button>
        </div>
      </header>

      <section className="flex-1 min-h-0">
        <div className="flex h-full flex-col rounded-xl border bg-card shadow-sm">
          <div className="flex items-center justify-between border-b border-border px-4 py-3">
            <div>
              <h2 className="text-base font-semibold">Drone Early Inform 설정</h2>
              <p className="text-sm text-muted-foreground">라인별 메인 스텝과 종료 스텝을 관리합니다.</p>
            </div>
            <Button variant="ghost" size="sm" onClick={handleRefresh} disabled={!hasLineId || isLoading}>
              <IconRefresh className="size-4" />
            </Button>
          </div>

          <div className="flex-1 space-y-4 overflow-auto px-4 py-4">
            {error && (
              <div className="flex items-center gap-2 rounded-md border border-destructive/40 bg-destructive/10 px-3 py-2 text-sm text-destructive">
                <IconAlertCircle className="size-4" />
                <span>{error}</span>
              </div>
            )}

            {!hasLineId ? (
              <div className="text-sm text-muted-foreground">좌측에서 라인을 선택하면 데이터가 표시됩니다.</div>
            ) : (
              <TableContainer className="rounded-lg border">
                <Table stickyHeader>
                  <TableHeader>
                    <TableRow className="bg-muted/40">
                      <TableHead className="w-[90px]">ID</TableHead>
                      <TableHead>Main Step</TableHead>
                      <TableHead>Custom End Step</TableHead>
                      <TableHead className="w-[160px] text-right">작업</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {isLoading && rows.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={4} className="text-center text-sm text-muted-foreground">
                          데이터를 불러오는 중입니다…
                        </TableCell>
                      </TableRow>
                    ) : rows.length === 0 ? (
                      <TableRow>
                        <TableCell colSpan={4} className="text-center text-sm text-muted-foreground">
                          등록된 설정이 없습니다.
                        </TableCell>
                      </TableRow>
                    ) : (
                      rows.map((row) => {
                        const draft = drafts[row.id]
                        const mainStepValue = draft?.mainStep ?? row.mainStep
                        const customEndStepValue = draft?.customEndStep ?? row.customEndStep
                        const isSaving = Boolean(savingIds[row.id])
                        const isDeleting = Boolean(deletingIds[row.id])
                        const hasChanges = hasDraftChanges(row.id)

                        return (
                          <TableRow key={row.id} data-state={hasChanges ? "editing" : undefined}>
                            <TableCell className="font-mono text-xs text-muted-foreground">{row.id}</TableCell>
                            <TableCell>
                              <Input
                                value={mainStepValue}
                                onChange={(event) => handleDraftChange(row.id, "mainStep", event.target.value)}
                                disabled={isSaving || isDeleting}
                                placeholder="메인 스텝"
                              />
                            </TableCell>
                            <TableCell>
                              <Input
                                value={customEndStepValue}
                                onChange={(event) => handleDraftChange(row.id, "customEndStep", event.target.value)}
                                disabled={isSaving || isDeleting}
                                placeholder="커스텀 종료 스텝"
                              />
                            </TableCell>
                            <TableCell>
                              <div className="flex items-center justify-end gap-2">
                                {draft ? (
                                  <Button
                                    variant="ghost"
                                    size="sm"
                                    onClick={() => handleCancelDraft(row.id)}
                                    disabled={isSaving || isDeleting}
                                    className="text-muted-foreground hover:text-destructive"
                                  >
                                    <IconX className="size-4" />
                                  </Button>
                                ) : null}
                                <Button
                                  variant="outline"
                                  size="sm"
                                  onClick={() => handleSaveRow(row.id)}
                                  disabled={!hasChanges || isSaving || isDeleting}
                                  className={cn({ "opacity-60": !hasChanges })}
                                >
                                  <IconDeviceFloppy className="size-4" />
                                  저장
                                </Button>
                                <Button
                                  variant="destructive"
                                  size="sm"
                                  onClick={() => handleDeleteRow(row.id)}
                                  disabled={isSaving || isDeleting}
                                >
                                  <IconTrash className="size-4" />
                                </Button>
                              </div>
                            </TableCell>
                          </TableRow>
                        )
                      })
                    )}

                    {hasLineId && (
                      <TableRow className="bg-muted/20">
                        <TableCell className="font-mono text-xs text-muted-foreground">신규</TableCell>
                        <TableCell>
                          <Input
                            value={newRow.mainStep}
                            onChange={(event) => handleNewRowChange("mainStep", event.target.value)}
                            placeholder="예: MAIN_STEP"
                            disabled={isCreating || isBusy}
                          />
                        </TableCell>
                        <TableCell>
                          <Input
                            value={newRow.customEndStep}
                            onChange={(event) => handleNewRowChange("customEndStep", event.target.value)}
                            placeholder="예: CUSTOM_END"
                            disabled={isCreating || isBusy}
                          />
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center justify-end">
                            <Button
                              size="sm"
                              onClick={handleCreateRow}
                              disabled={isCreating || !hasLineId}
                            >
                              <IconPlus className="size-4" />
                              추가
                            </Button>
                          </div>
                        </TableCell>
                      </TableRow>
                    )}
                  </TableBody>
                </Table>
              </TableContainer>
            )}

            {createError && (
              <div className="flex items-center gap-2 rounded-md border border-destructive/40 bg-destructive/10 px-3 py-2 text-sm text-destructive">
                <IconAlertCircle className="size-4" />
                <span>{createError}</span>
              </div>
            )}
          </div>
        </div>
      </section>
    </div>
  )
}
